<h1 id="vue" tabindex="-1"><a class="header-anchor" href="#vue" aria-hidden="true">#</a> Vue</h1>
<h3 id="_12-webpack的使用" tabindex="-1"><a class="header-anchor" href="#_12-webpack的使用" aria-hidden="true">#</a> 12 webpack的使用</h3>
<p>webpack：模块化的打包工具</p>
<h4 id="_01-起步" tabindex="-1"><a class="header-anchor" href="#_01-起步" aria-hidden="true">#</a> 01-起步</h4>
<p>目录结构<br>
<img src="https://cdn.jsdelivr.net/gh/oyw6719119/gitbookpig/202110041421351.png" alt=""></p>
<p>webpack安装</p>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code><span class="token function">npm</span> <span class="token function">install</span> webpack@3.6.0 -g <span class="token comment">#-g表示全局安装</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><p>一个js就是一个模块，main.js是入口文件，bundle.js是出口文件</p>
<p>打包</p>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code>webpack ./src/main.js ./dist/bundle.js
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><h4 id="_03-webpack的loader" tabindex="-1"><a class="header-anchor" href="#_03-webpack的loader" aria-hidden="true">#</a> 03-webpack的loader</h4>
<h5 id="_01-加载less文件" tabindex="-1"><a class="header-anchor" href="#_01-加载less文件" aria-hidden="true">#</a> 01 加载less文件</h5>
<div class="language-javascript ext-js line-numbers-mode"><pre v-pre class="language-javascript"><code><span class="token function">require</span><span class="token punctuation">(</span><span class="token string">'./css/specil.less'</span><span class="token punctuation">)</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><p>打包，报错，没有导入loader</p>
<p><img src="https://cdn.jsdelivr.net/gh/oyw6719119/gitbookpig/202110041542850.png" alt="image-20211004154238816"></p>
<p>安装loader，考虑兼容问题，带版本号，具体可看<a href="https://www.webpackjs.com/loaders/less-loader/" target="_blank" rel="noopener noreferrer">中文文档<OutboundLink/></a></p>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code><span class="token function">npm</span> <span class="token function">install</span> --save-dev less-loader@4.1.0 less@3.9.0
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><p>再次打包没报错，成功显示。</p>
<h5 id="_02-加载本地图片" tabindex="-1"><a class="header-anchor" href="#_02-加载本地图片" aria-hidden="true">#</a> 02 加载本地图片</h5>
<div class="language-css ext-css line-numbers-mode"><pre v-pre class="language-css"><code><span class="token property">background-image</span><span class="token punctuation">:</span> <span class="token url"><span class="token function">url</span><span class="token punctuation">(</span><span class="token string url">'../img/v2-ecbcec922e08f5f52f4b20e9b7211fb2_r.jpg'</span><span class="token punctuation">)</span></span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><p><em>url-loader图片导入使用base64编码</em></p>
<p>显然报错，导入loader，考虑兼容问题，带版本号，具体可看<a href="https://www.webpackjs.com/loaders/url-loader/" target="_blank" rel="noopener noreferrer">中文文档<OutboundLink/></a></p>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code><span class="token function">npm</span> <span class="token function">install</span> --save-dev url-loader@1.1.2
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><p>修改<strong>webpack.config.js</strong></p>
<p>再次打包，依然报错</p>
<p><img src="https://cdn.jsdelivr.net/gh/oyw6719119/gitbookpig/202110041616516.png" alt="image-20211004161633488"></p>
<p>官网说明</p>
<p>If the file is greater than the limit (in bytes) the <a href="https://www.webpackjs.com/loaders/file-loader/" target="_blank" rel="noopener noreferrer"><code>file-loader</code><OutboundLink/></a> is used by default and all query parameters are passed to it. You can use other loader using <code>fallback</code> option.</p>
<p>超过limit设定的最大文件大小使用file-loader，设置更高数值或者安装file-loader</p>
<p>安装file-loader</p>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code><span class="token function">npm</span> <span class="token function">install</span> file-loader@3.0.1 --save-dev
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><p>打包，网页没有成功加载背景图片，output目录(/dist)与图片加载目录不一致(/)</p>
<p>修改webpack-config.js</p>
<div class="language-javascript ext-js line-numbers-mode"><pre v-pre class="language-javascript"><code><span class="token keyword">const</span> path <span class="token operator">=</span> <span class="token function">require</span><span class="token punctuation">(</span><span class="token string">'path'</span><span class="token punctuation">)</span>
module<span class="token punctuation">.</span>exports <span class="token operator">=</span> <span class="token punctuation">{</span>
  entry<span class="token operator">:</span> <span class="token string">'./src/main.js'</span><span class="token punctuation">,</span>
  output<span class="token operator">:</span> <span class="token punctuation">{</span>
    path<span class="token operator">:</span> path<span class="token punctuation">.</span><span class="token function">resolve</span><span class="token punctuation">(</span>__dirname<span class="token punctuation">,</span> <span class="token string">'dist'</span><span class="token punctuation">)</span><span class="token punctuation">,</span>
    filename<span class="token operator">:</span> <span class="token string">'bundle.js'</span><span class="token punctuation">,</span>
    <span class="token comment">//新增</span>
    publicPath<span class="token operator">:</span> <span class="token string">'dist/'</span>
  <span class="token punctuation">}</span> 
<span class="token punctuation">}</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br></div></div><p>重新打包，成功显示</p>
<p><strong>PS：外链可直接访问，无需打包</strong></p>
<p>图片命名-webpack-config.js<a href="https://www.webpackjs.com/loaders/file-loader/#placeholders" target="_blank" rel="noopener noreferrer">文档<OutboundLink/></a>：</p>
<div class="language-javascript ext-js line-numbers-mode"><pre v-pre class="language-javascript"><code>use<span class="token operator">:</span> <span class="token punctuation">[</span>
    <span class="token punctuation">{</span>
        loader<span class="token operator">:</span> <span class="token string">'url-loader'</span><span class="token punctuation">,</span>
        options<span class="token operator">:</span> <span class="token punctuation">{</span>
            limit<span class="token operator">:</span> <span class="token number">8192</span><span class="token punctuation">,</span>
            name<span class="token operator">:</span> <span class="token string">'./img/[name][hash:8].[ext]'</span><span class="token punctuation">,</span>
        <span class="token punctuation">}</span><span class="token punctuation">,</span>
    <span class="token punctuation">}</span><span class="token punctuation">,</span>
<span class="token punctuation">]</span><span class="token punctuation">,</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br></div></div><h5 id="_03-es6语法处理" tabindex="-1"><a class="header-anchor" href="#_03-es6语法处理" aria-hidden="true">#</a> 03 ES6语法处理</h5>
<p>安装babel-loader，<a href="https://www.webpackjs.com/loaders/babel-loader/" target="_blank" rel="noopener noreferrer">document<OutboundLink/></a></p>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code><span class="token function">npm</span> <span class="token function">install</span> --save-dev babel-loader@7 babel-core babel-preset-es2015
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><p>rule添加后修改参数，原参数需要安装preset-env</p>
<div class="language-javascript ext-js line-numbers-mode"><pre v-pre class="language-javascript"><code>options<span class="token operator">:</span> <span class="token punctuation">{</span>
    presets<span class="token operator">:</span> <span class="token punctuation">[</span><span class="token string">'es2015'</span><span class="token punctuation">]</span><span class="token punctuation">,</span>
<span class="token punctuation">}</span><span class="token punctuation">,</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br></div></div><p>打包，显示成功</p>
<h4 id="_04-webpack配置vue" tabindex="-1"><a class="header-anchor" href="#_04-webpack配置vue" aria-hidden="true">#</a> 04-webpack配置vue</h4>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code><span class="token function">npm</span> <span class="token function">install</span> vue --save	<span class="token comment">#vue是运行时依赖</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><div class="language-javascript ext-js line-numbers-mode"><pre v-pre class="language-javascript"><code><span class="token comment">//main.js</span>
<span class="token keyword">import</span> Vue <span class="token keyword">from</span> <span class="token string">'vue'</span>
<span class="token keyword">const</span> app <span class="token operator">=</span> <span class="token keyword">new</span> <span class="token class-name">Vue</span><span class="token punctuation">(</span><span class="token punctuation">{</span>
  el<span class="token operator">:</span> <span class="token string">'#app'</span><span class="token punctuation">,</span>
  data<span class="token operator">:</span> <span class="token punctuation">{</span>
    message<span class="token operator">:</span> <span class="token string">'我是oyw'</span><span class="token punctuation">,</span>
  <span class="token punctuation">}</span><span class="token punctuation">,</span>
<span class="token punctuation">}</span><span class="token punctuation">)</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br></div></div><div class="language-html ext-html line-numbers-mode"><pre v-pre class="language-html"><code><span class="token comment">&lt;!-- index.html --></span>
<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>div</span> <span class="token attr-name">id</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>app<span class="token punctuation">"</span></span><span class="token punctuation">></span></span>
    <span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>h2</span><span class="token punctuation">></span></span>{{message}}<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;/</span>h2</span><span class="token punctuation">></span></span>
<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;/</span>div</span><span class="token punctuation">></span></span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br></div></div><p>console报错</p>
<div class="language-error ext-error line-numbers-mode"><pre v-pre class="language-error"><code>[Vue warn]: You are using the runtime-only build of Vue where the template compiler is not available. Either pre-compile the templates into render functions, or use the compiler-included build.
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><p>当前vue的环境不支持解析template，需要换个环境</p>
<div class="language-javascript ext-js line-numbers-mode"><pre v-pre class="language-javascript"><code><span class="token comment">//webpack-config.js</span>
module<span class="token operator">:</span><span class="token punctuation">{</span><span class="token operator">...</span><span class="token punctuation">}</span><span class="token punctuation">,</span>
resolve<span class="token operator">:</span> <span class="token punctuation">{</span>
  alias<span class="token operator">:</span> <span class="token punctuation">{</span>
    vue$<span class="token operator">:</span> <span class="token string">'vue/dist/vue.esm.js'</span><span class="token punctuation">,</span>
  <span class="token punctuation">}</span><span class="token punctuation">,</span>
<span class="token punctuation">}</span><span class="token punctuation">,</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br></div></div><p><strong>PS：打包的js(</strong> <em>bundle.js</em> <strong>)导入到html的顺序很重要，会影响网页的渲染，建议放在最后</strong></p>
<h5 id="_01-尽量在vue组件中写html代码" tabindex="-1"><a class="header-anchor" href="#_01-尽量在vue组件中写html代码" aria-hidden="true">#</a> 01 尽量在vue组件中写html代码</h5>
<p>减少修改html中的代码，尽量在vue中书写代码</p>
<div class="language-html ext-html line-numbers-mode"><pre v-pre class="language-html"><code><span class="token comment">&lt;!-- index.html --></span>
<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>div</span> <span class="token attr-name">id</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>app<span class="token punctuation">"</span></span><span class="token punctuation">></span></span>
<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;/</span>div</span><span class="token punctuation">></span></span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br></div></div><div class="language-javascript ext-js line-numbers-mode"><pre v-pre class="language-javascript"><code><span class="token comment">//main.js </span>
<span class="token keyword">const</span> app <span class="token operator">=</span> <span class="token keyword">new</span> <span class="token class-name">Vue</span><span class="token punctuation">(</span><span class="token punctuation">{</span>
  el<span class="token operator">:</span> <span class="token string">'#app'</span><span class="token punctuation">,</span>
  template<span class="token operator">:</span> <span class="token template-string"><span class="token template-punctuation string">`</span><span class="token string">
    &lt;div>
      &lt;h2>{{message}}&lt;/h2>
      &lt;button>按钮&lt;/button>
    &lt;/div>
  </span><span class="token template-punctuation string">`</span></span><span class="token punctuation">,</span>
  data<span class="token operator">:</span> <span class="token punctuation">{</span>
    message<span class="token operator">:</span> <span class="token string">'我是oyw'</span><span class="token punctuation">,</span>
  <span class="token punctuation">}</span><span class="token punctuation">,</span>
<span class="token punctuation">}</span><span class="token punctuation">)</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br><span class="line-number">13</span><br></div></div><h5 id="_02-vue的终极使用方案" tabindex="-1"><a class="header-anchor" href="#_02-vue的终极使用方案" aria-hidden="true">#</a> 02 Vue的终极使用方案</h5>
<p>以普通js代码实现代码模块化和组件化</p>
<div class="language-javascript ext-js line-numbers-mode"><pre v-pre class="language-javascript"><code><span class="token comment">// src/main.js</span>
<span class="token keyword">import</span> Vue <span class="token keyword">from</span> <span class="token string">'vue'</span>
<span class="token keyword">import</span> App <span class="token keyword">from</span> <span class="token string">'./vue/vue'</span>
<span class="token keyword">const</span> app <span class="token operator">=</span> <span class="token keyword">new</span> <span class="token class-name">Vue</span><span class="token punctuation">(</span><span class="token punctuation">{</span>
  el<span class="token operator">:</span> <span class="token string">'#app'</span><span class="token punctuation">,</span>
  template<span class="token operator">:</span> <span class="token string">'&lt;App/>'</span><span class="token punctuation">,</span>
  components<span class="token operator">:</span> <span class="token punctuation">{</span>
    App<span class="token punctuation">,</span>
  <span class="token punctuation">}</span><span class="token punctuation">,</span>
<span class="token punctuation">}</span><span class="token punctuation">)</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br></div></div><div class="language-javascript ext-js line-numbers-mode"><pre v-pre class="language-javascript"><code><span class="token comment">// src/vue/vue.js</span>
<span class="token keyword">export</span> <span class="token keyword">default</span> <span class="token punctuation">{</span>
  template<span class="token operator">:</span> <span class="token template-string"><span class="token template-punctuation string">`</span><span class="token string">...</span><span class="token template-punctuation string">`</span></span><span class="token punctuation">,</span>
  <span class="token function">data</span><span class="token punctuation">(</span><span class="token punctuation">)</span> <span class="token punctuation">{</span><span class="token operator">...</span><span class="token punctuation">}</span><span class="token punctuation">,</span>
  methods<span class="token operator">:</span> <span class="token punctuation">{</span><span class="token operator">...</span><span class="token punctuation">}</span><span class="token punctuation">,</span>
<span class="token punctuation">}</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br></div></div><p>打包，成功显示</p>
<p>以vue文件实现</p>
<p>安装loader，带上版本</p>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code><span class="token function">npm</span> <span class="token function">install</span> --save-dev vue-loader@15.4.2 vue-template-compiler@2.5.21
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><p>打包，报错</p>
<div class="language-error ext-error line-numbers-mode"><pre v-pre class="language-error"><code>vue-loader was used without the corresponding plugin. Make sure to include VueLoaderPlugin in your webpack config.
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><p><a href="https://vue-loader.vuejs.org/zh/migrating.html#%E5%80%BC%E5%BE%97%E6%B3%A8%E6%84%8F%E7%9A%84%E4%B8%8D%E5%85%BC%E5%AE%B9%E5%8F%98%E6%9B%B4" target="_blank" rel="noopener noreferrer">安装插件<OutboundLink/></a></p>
<p>打包，运行依然报错</p>
<div class="language-error ext-error line-numbers-mode"><pre v-pre class="language-error"><code>[vue-loader] vue-template-compiler must be installed as a peer dependency, or a compatible compiler implementation must be passed via options.
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><p>vue与vue-template-compiler版本不一致，重装</p>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code><span class="token function">npm</span> <span class="token function">install</span> vue@2.5.21 vue-template-compiler@2.5.21 --save 
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><p>打包，成功显示</p>
<h4 id="_05-webpack配置plugins" tabindex="-1"><a class="header-anchor" href="#_05-webpack配置plugins" aria-hidden="true">#</a> 05-webpack配置Plugins</h4>
<h5 id="_01-横幅插件" tabindex="-1"><a class="header-anchor" href="#_01-横幅插件" aria-hidden="true">#</a> 01 横幅插件</h5>
<div class="language-javascript ext-js line-numbers-mode"><pre v-pre class="language-javascript"><code><span class="token comment">// /webpack-config.js</span>
<span class="token keyword">const</span> webpack <span class="token operator">=</span> <span class="token function">require</span><span class="token punctuation">(</span><span class="token string">'webpack'</span><span class="token punctuation">)</span>
module<span class="token punctuation">.</span>exports <span class="token operator">=</span> <span class="token punctuation">{</span>
  <span class="token operator">...</span>
  plugins<span class="token operator">:</span> <span class="token punctuation">[</span>
    <span class="token keyword">new</span> <span class="token class-name">webpack<span class="token punctuation">.</span>BannerPlugin</span><span class="token punctuation">(</span><span class="token string">'最终解释权归xxx所有'</span><span class="token punctuation">)</span><span class="token punctuation">,</span>
  <span class="token punctuation">]</span><span class="token punctuation">,</span>
<span class="token punctuation">}</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br></div></div><p><img src="https://cdn.jsdelivr.net/gh/oyw6719119/gitbookpig/202110042157715.png" alt="image-20211004215718643"></p>
